body {
  padding: 0;
  margin: 0;
  font-family: 'PT Mono', serif;
  color: #ffffff;
  overflow: hidden; /* Prevent scrolling on mobile */
}

h1, p {
  font-family: Lato;
}

canvas {
  display: block;
  background-color: #000000;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
}

.controls {
  display: block;
  position: absolute;
  top: 15px;
  left: 50%;
  transform: translate(-50%, 0);
  z-index: 1;
  font-size: 20px;
  text-align: center;
  line-height: 1.6;
}

.avatar {
  display: block;
  position: absolute;
  top: 25px;
  left: 2%;
  font-size: 20px;
  z-index: 1;
  
}

.badges {
  display: block;
  position: absolute;
  width: 100px;
  height: 100px;
  border-radius:50%;
  z-index: 2;
  
}

.badge-effect:hover {
  transform: scale(1.2); 
  filter: grayscale(0%);
  filter: gray;
  -webkit-filter: grayscale(0%);
  filter: none;
  transition: 0.1s ease;
}

.badges.linkedin {
  bottom: 50px;
  left: 5%;
}

.badges.github {
  bottom: 50px;
  right: 5%;
}

.badges.xing {
  top: 50px;
  right: 5%;
}

/* Mobile responsiveness */
@media (max-width: 768px) {
  .controls {
    font-size: 14px; /* Smaller font size for controls on small screens */
    top: 5px; /* Adjust vertical position */
    line-height: 1.2;
  }

  .avatar {
    top: 10px; /* Adjust vertical position */
    left: 50%; /* Center horizontally */
    transform: translateX(-50%);
    width: 90%; /* Take up more width */
    text-align: center;
  }

  /* Adjust badge sizes and positions for mobile */
  .badges {
    width: 70px;
    height: 70px;
  }

  .badges.linkedin {
    bottom: 20px;
    left: 5%;
  }

  .badges.github {
    bottom: 20px;
    right: 5%;
  }

  .badges.xing {
    top: 70px;
    right: 5%;
  }
}
